<template>
  <v-container fluid class="ma-8">
    <v-row>
      <v-col cols="12" md="4" center>
        <v-row>
          <v-col cols="12" class="d-flex justify-center">
            <v-hover>
              <template v-slot:default="{ hover }">
                <v-avatar size="180" class="avatar-border">
                  <img :src="currentUser.avatar" v-if="currentUser.avatar" />

                  <span class="text-h1" v-else>
                    <!-- , -->
                    {{ currentUser.username[0].toUpperCase() }}
                  </span>
                  <v-fade-transition>
                    <v-overlay v-if="hover" absolute>
                      <v-btn
                        height="180"
                        width="180"
                        large
                        text
                        fab
                        @click.stop="avatarDialog = true"
                      >
                        
                      </v-btn>
                    </v-overlay>
                  </v-fade-transition>
                </v-avatar>
              </template>
            </v-hover>
          </v-col>
          <v-col cols="12" class="d-flex justify-center">
            <span class="text-h6">
              {{ currentUser.username }}
            </span>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12" md="8">
        <v-row>
          <v-col>
            <span class="text-h5"></span>
            <v-divider />
          </v-col>
          <v-col cols="12">
            <span
              :class="
                currentUser.mobilePhoneVerified ? 'green--text' : 'red--text'
              "
            >
              ({{ currentUser.mobilePhoneVerified | verified }})
            </span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.mobilePhoneNumber"
            >
            </v-text-field>
          </v-col>
          <v-col cols="12" sm="6" md="4">
            <v-dialog v-model="phoneNumberDialog" width="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn v-bind="attrs" v-on="on" class="mr-3">  </v-btn>
              </template>
              <v-card>
                <v-card-title class="headline grey lighten-2" primary-title>
                  
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field label=""></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="phoneNumberDialog = false"
                  >
                    
                  </v-btn>
                  <v-btn
                    color="success"
                    text
                    @click="phoneNumberDialog = false"
                  >
                    
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-btn v-if="!currentUser.mobilePhoneVerified"></v-btn>
          </v-col>
          <v-col cols="12">
            
            <span
              :class="currentUser.emailVerified ? 'green--text' : 'red--text'"
            >
              ({{ currentUser.emailVerified | verified }})
            </span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.email"
            >
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-btn v-if="!currentUser.emailVerified"></v-btn>
          </v-col>
          <v-col cols="12">
            <span></span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field single-line outlined disabled dense value="********">
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-dialog v-model="passwordDialog" persistent max-width="600px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn v-bind="attrs" v-on="on">  </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline"></span>
                </v-card-title>
                <v-divider />
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          label="*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          label="*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          label="*"
                          type="password"
                          required
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                  <small class="red--text">* </small>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="passwordDialog = false"
                    >Close</v-btn
                  >
                  <v-btn color="success" text @click="passwordDialog = false"
                    >Save</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-col>
          <v-col cols="12">
            <span></span>
          </v-col>
          <v-col sm="6" md="4">
            <v-text-field
              single-line
              outlined
              counter
              disabled
              dense
              :value="currentUser.nickname"
            >
            </v-text-field>
          </v-col>
          <v-col sm="6" md="4">
            <v-btn></v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-dialog v-model="avatarDialog" hide-overlay width="600">
      <v-card>
        <v-card-title class="headline">
          <span></span>
          <v-spacer />
          <v-btn icon @click="avatarDialog = false">
            <v-icon color="red">mdi-close-thick</v-icon>
          </v-btn>
        </v-card-title>

        <v-divider />
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="8">
                <div
                  class="place"
                  @click="updateImage"
                  v-if="image.length === 0"
                >
                  <div class="place-circle"></div>
                </div>
                <v-img
                  :src="image"
                  height="300"
                  contain
                  border="2"
                  v-else
                ></v-img>
              </v-col>
              <v-col cols="4">
                <p class="text-center"></p>
                <v-avatar size="160" class="avatar-border">
                  <img
                    :src="
                      image.length === 0
                        ? $store.state.currentUser.avatar
                        : image
                    "
                  />
                </v-avatar>
                <p class="text-center">160 x 160</p>
              </v-col>
            </v-row>
          </v-container>
          <v-overlay :value="updateOverlay">
            <v-progress-circular indeterminate size="64"></v-progress-circular>
          </v-overlay>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <input
            accept="image/*"
            type="file"
            ref="selectFile"
            hidden
            @change="loadImageFile"
          />
          <v-btn color="green darken-1" text @click="updateImage">  </v-btn>

          <v-btn
            color="orange darken-4 white--text"
            @click="saveAvatar"
            :disabled="imageFile === ''"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import AccountService from "@/service/account_service";
export default {
  name: "user-info",
  data: () => ({
    // , {} 
    currentUser: {
      username: "admin",
      email: "abc@163.com",
      emailVerified: false,
      mobilePhoneNumber: "1373737910640",
      mobilePhoneVerified: true,
      password: "******",
      nickname: "Admin",
      avatar: require("@/assets/avatar.png"), // require 
    },
    
    avatarDialog: false, // 
    phoneNumberDialog: false, // 
    passwordDialog: false, // 
    phoneNumber: "",
    password: "********",
    reenterpassword: "",
    image: [], // 
    imageFile: "", // 
    updateOverlay: false, // 
  }),
  mounted() {
    this.getCurrentUser();
  },
  methods: {
    getCurrentUser() {
      this.currentUser = AccountService.currentUser();
    },

    // 
    updateImage() {
      //  
      // selectFile  input   ref="selectFile" 
      this.$refs.selectFile.click();
    },

    // 
    loadImageFile() {
      console.log("load image...");
      // selectFile  input   ref="selectFile" 
      this.imageFile = this.$refs.selectFile.files[0];

      // url
      this.image = window.URL.createObjectURL(this.imageFile);
    },

    // 
    saveAvatar() {
      this.updateOverlay = true;
      if (this.currentUser.id && this.imageFile !== "") {
        console.log(this.imageFile);
        AccountService.updateAvatar(this.imageFile)
          .then((user) => {
            //
            this.currentUser = user;
            //  vuex 
            this.$store.commit("SET_CURRENT_USER", user);
            this.avatarClose();
          })
          .catch((error) => {
            this.avatarClose();
            //
          });
      }
    },

    // 
    avatarClose() {
      this.avatarDialog = false;
      this.updateOverlay = false;
      this.image = [];
      this.imageFile = "";
    },
  },

  watch: {
    // 
    avatarDialog(val) {
      val || this.avatarClose();
    },
  },
  // 
  filters: {
    verified: function (val) {
      if (val) {
        return "";
      } else {
        return "";
      }
    },
  },
};
</script>

<style scoped>
.avatar-border {
  border: 2px solid gray;
}

.place {
  width: 300px;
  height: 300px;
  background-color: gray;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.place-circle {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  background-color: white;
}
</style>